n}\n.teaserBlock {\n  width: 100%;\n  @include mut.rem-margined(13px, 3px, 0px, 0px);\n  @include mut.rem-padded(0px, 5px, 0px, 0px);\n  line-height: 23px;\n  max-height: f.pxToRem(214px);\n  overflow: auto;\n  color: c.$lighter-black-color;\n  float: left;\n  &::-webkit-scrollbar {\n    display: none;\n  }\n}\n\n.teaserBlockContainer {\n    position: relative;\n    height: f.pxToRem(214px);\n\n    \n}\n\n.cardFooter {\n    width: 100%;\n    position: relative;\n    @include mut.rem-padded(10px, 0px, 54px, 0px);\n    @include mut.flexed($justify-content: flex-start, $gap: 20px);\n  .ant-btn-primary:disabled {\n    background-color: c.$lighter-gray-color;\n    color: c.$pure-white-color;\n    }\n  .ant-btn-default {\n    border-width: 3px;\n    border-color: c.$accented-red-color;\n  }\n  &:before {\n    content: '';\n    position: absolute;\n    bottom: 45px;\n    left: 0;\n    right: 5px;\n    height: 25px;\n    box-shadow: none;\n    background: linear-gradient(0deg, rgba(255,255,255,0.01) 0%, rgba(255,255,255,0.25) 5%, rgba(255,255,255,0.75) 21%);\n    -webkit-text-fill-color: transparent;\n}\n\n}\n.audioBtnActive {\n  background-color: c.$accented-red-color;\n  border-color: c.$accented-red-color;\n  &:hover {\n    box-shadow: 0 4px 4px rgba(219, 52, 36, 0.18);\n  }\n}\n.audioBtn {\n    @include mut.sized(271px, 73px);\n    @include mut.flex-centered();\n    @include mut.full-rounded(14px);\n    @include mut.with-font($font-weight: 500);\n}\n.audioBtn:disabled {\n    cursor: default;\n}\n.animateFigureBtn {\n  @include mut.sized(229px, 73px);\n  @include mut.flex-centered();\n  @include mut.full-rounded(14px);\n  a {\n    @include mut.with-font($font-weight: 500);\n    border-width: 3px;\n    border-color: c.$accented-red-color;\n    color: c.$accented-red-color;\n  }\n  &:hover {\n    filter: drop-shadow(0px 4px 4px rgba(219, 52, 36, 0.1));\n  }\n}\n.streetcodeImg {\n    margin: auto;\n    @include mut.sized(415px, 542px);\n}\n.playCircle {\n    font-size: f.pxToRem(37px);\n    margin-right: f.pxToRem(10px);\n}\n\n@media screen and (max-width: 1300px) {\n    .blockCentering {\n        width: 100%;\n        @include mut.rem-padded($top: 0px, $right: 10px, $bottom: 0px, $left: 10px);\n        margin: auto;\n\n        .headerContainer {\n            @include mut.rem-padded($top: 20px, $right: 40px, $bottom: 10px, $left: 7px);\n        }\n    }\n}\n@media screen and (max-width: 1210px) {\n    .blockCentering {\n        .headerContainer {\n            @include mut.rem-padded($top: 20px, $right: 15px, $bottom: 10px, $left: 0px);\n        }\n\n        .teaserBlock {\n            margin-right: f.pxToRem(30px);\n        }\n\n        .rightSider {\n            margin-top: f.pxToRem(0px);\n            align-self: center;\n        }\n\n        .cardFooter {\n            gap: f.pxToRem(10px);\n\n            .audioBtn {\n                @include mut.sized(255px, 73px);\n                @include mut.flex-centered();\n            }\n        }\n    }\n}\n@media screen and (max-width: 1060px) {\n  .blockCentering {\n  @include mut.rem-padded($top: 0px, $right: 0px, $bottom: 0px, $left: 0px);\n  }\n}\n@media screen and (max-width: 1024px) {\n  .blockCentering {\n    @include mut.rem-padded($top: 0px, $right: 10px, $bottom: 0px, $left: 10px);\n    .headerContainer {\n      @include mut.rem-padded($top: 0px, $right: 10px, $bottom: 0px, $left: 21px);\n      .tagContainer{\n        max-height: f.pxToRem(82px);\n      }\n      .upper-info{\n        max-height: f.pxToRem(433px);\n      }\n    }\n    .teaserBlock {\n      line-height: f.pxToRem(19px);\n      max-height: f.pxToRem(214px);\n      @include mut.with-font($font-weight: 300, $font-size: 16px);\n    }\n    .card {\n      @include mut.sized(100%, 495px);\n      background-color: rgba(0, 0, 0, 0) !important;\n      box-shadow: none;\n      .rightSider{\n        padding: 0;\n        height: f.pxToRem(489px);\n      }\n      .leftSider {\n        @include mut.sized(360px, 489px);\n        flex: 0 0 f.pxToRem(360px);\n        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);\n        .leftSiderContent {\n          background-color: c.$accented-gray-color;\n          @include mut.border(c.$pure-white-color,10px,solid);\n        }\n      }\n      .streetcodeIndex {\n        @include mut.rem-padded($top: 0px, $right: 0px, $bottom: 5px, $left: 0px);\n        @include mut.with-font($font-weight: 300, $font-size: 15px);\n      }\n      .streetcodeTitle {\n        @include mut.rem-padded(0px, 10px, 0px, 0px);\n        @include mut.with-font(ft.$closer-text-font, $font-weight: 500, $font-size: 32px);\n        @include mut.withLineHeight(42px);\n      }\n      .streetcodeDate {\n        @include mut.rem-padded(0px, 5px, 0px, 0px);\n        @include mut.with-font(ft.$roboto-font, $font-weight: 400, $font-size: 20px);\n        @include mut.withLineHeight(23px);\n      }\n      .streetcodeImg {\n        @include mut.sized(290px, 377px);\n      }\n      .cardFooter {\n        width: 98%;\n        @include mut.rem-padded(10px, 0px, 0px, 0px);\n        .animateFigureBtn {\n          @include mut.hidden();\n        }\n        .audioBtn {\n          @include mut.full-rounded(8px);\n          @include mut.with-font($font-weight: 400, $font-size: 14px);\n          @include mut.sized(95%, 46px);\n          .playCircle {\n            font-size: f.pxToRem(23px);\n            margin-right: f.pxToRem(10px);\n          }\n        }\n      }\n    }\n  }\n}\n@media screen and (max-width: 680px) {\n  .blockCentering{\n    .card{\n      width: 100%;\n      height: unset ;\n      @include mut.full-rounded(0px);\n      .rightSider {\n        width: 100%;\n        height: unset ;\n      }\n      .cardFooter{\n        margin-top: f.pxToRem(39px) !important;\n        @include mut.rem-padded(0px, 0px, 5px, 0px);\n        .audioBtn{\n          width: 100%;\n        }\n      }\n      .teaserBlock {\n        max-height: f.pxToRem(240px);\n\n      }\n      .headerContainer{\n        @include mut.rem-padded($top: 40px, $right: 0px, $bottom: 0px, $left: 0px);\n        @include mut.full-rounded(0px);\n\n      }\n    }\n  }\n}","@use '../variables/variables.fonts' as ft;\n@use '../variables/variables.colors' as c;\n@use '../mixins/vendor.mixins' as vnd;\n@use '../utils.functions' as f;\n\n// --------------------------Positioning--------------------------\n@mixin flexed(\n  $direction: row,\n  $align-items: stretch,\n  $justify-content: stretch,\n  $wrap: nowrap,\n  $gap: 0\n) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify-content;\n  align-items: $align-items;\n  flex-wrap: $wrap;\n  gap: f.pxToRem($gap);\n}\n\n\n@mixin set-positioning($top, $right, $bottom, $left) {\n  @if ($top == 'auto' or unit($top) == '%') {\n    top: $top;\n  } @else {\n    top: f.pxToRem($top);\n  }\n  @if ($right == 'auto' or unit($right) == '%') {\n    right: $right;\n  } @else {\n    right: f.pxToRem($right);\n  }\n  @if ($bottom == 'auto' or unit($bottom) == '%') {\n    bottom: $bottom;\n  } @else {\n    bottom: f.pxToRem($bottom);\n  }\n  @if ($left == 'auto' or unit($left) == '%') {\n    left: $left;\n  } @else {\n    left: f.pxToRem($left);\n  }\n}\n\n@mixin positioned-as($position: relative, $top: auto, $right: auto, $bottom: auto, $left: auto) {\n  position: $position;\n  @include set-positioning($top, $right, $bottom, $left);\n}\n\n@mixin sized($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width ;\n  } @else {\n    width: f.pxToRem($width) ;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height ;\n  } @else {\n    height: f.pxToRem($height);\n  }\n}\n@mixin sizedImportant($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width !important ;\n  } @else {\n    width: f.pxToRem($width) !important ;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height !important ;\n  } @else {\n    height: f.pxToRem($height) !important;\n  }\n}\n\n@mixin parent-sized {\n  @include sized(100%, 100%);\n}\n\n// --------------------------Centering----------------------------\n@mixin flex-centered($direction: row, $gap: 0) {\n  @include flexed($direction, center, center, nowrap, $gap);\n}\n\n@mixin absolute-centered($axes: 'both') {\n  position: absolute;\n\n  @if $axes == 'vertical' or $axes == 'v' {\n    top: 50%;\n    @include vnd.vendored(transform, translateY(-50%));\n  }\n  @else if $axes == 'horizontal' or $axes == 'h' {\n    left: 50%;\n    @include vnd.vendored(transform, translateX(-50%));\n  }\n  @else if $axes == 'both' or $axes == 'b' {\n    top: 50%;\n    left: 50%;\n    @include vnd.vendored(transform, translate(-50%, -50%));\n  }\n}\n\n// --------------------------Bordering----------------------------\n@mixin rounded(\n  $top-left: 0,\n  $top-right: 0,\n  $bottom-right: 0,\n  $bottom-left: 0,\n  $overflow: hidden\n) {\n  $rad: $top-left $top-right $bottom-right $bottom-left;\n\n  border-radius: $rad !important;\n  -webkit-border-radius: $rad !important;\n\n  overflow: $overflow !important;\n  -webkit-background-clip: padding-box;\n}\n@mixin border($color:\"#000\", $border-width:1px, $type:solid ){\n  border:  f.pxToRem($border-width) $type $color;\n}\n\n@mixin full-rounded($rad, $overflow: hidden) {\n  @include rounded($rad, $rad, $rad, $rad, $overflow);\n}\n\n@mixin circular($radius, $color: c.$pure-white-color) {\n  @include sized($radius, $radius);\n  @include full-rounded(50%);\n  background: $color;\n}\n\n// --------------------------Margins-----------------------------\n@mixin rem-margined($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  margin: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n\n@mixin rem-padded($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  padding: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n// --------------------------Appearance----------------------------\n@mixin backdrop-filtering-blur($f-size){\n  -webkit-backdrop-filter: blur($f-size);\n  -o-backdrop-filter: blur($f-size);\n  -moz-backdrop-filter: blur($f-size);\n\n  backdrop-filter: blur($f-size);\n}\n\n@mixin outlined($b-size: 2px, $color: red) {\n  outline: $b-size solid $color !important;\n}\n\n@mixin hidden {\n  display: none !important;\n  visibility: hidden !important;\n}\n\n@mixin visible($display: block) {\n  display: $display !important;\n  visibility: visible !important;\n}\n\n@mixin bg-image($file-path, $bg-size: cover, $bg-repeat: no-repeat) {\n  background-image: url($file-path);\n  background-repeat: $bg-repeat;\n  background-size: $bg-size;\n}\n\n@mixin colored($color: c.$pure-black-color, $bg-color: c.$pure-white-color) {\n  color: $color;\n  background-color: $bg-color;\n}\n// --------------------------Text----------------------------------\n@mixin withLineHeight($line-height){\n  line-height:f.pxToRem($line-height);\n}\n@mixin truncated($line-num: 3, $text-overflow: ellipsis) {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: $text-overflow;\n\n  @supports (-webkit-line-clamp: $line-num) {\n    overflow: hidden;\n    text-overflow: $text-overflow;\n    white-space: initial;\n    display: -webkit-box;\n    -webkit-line-clamp: $line-num;\n    -webkit-box-orient: vertical;\n  }\n}\n\n// --------------------------Fonts---------------------------------\n@mixin with-font(\n  $font-family: ft.$roboto-font,\n  $font-weight: normal,\n  $font-size: 20px,\n  $font-style: ft.$roboto-font-style\n) {\n  font-family: $font-family;\n  // px equivalent for browsers without rem support\n  font-size: f.pxToRem($font-size);\n  font-weight: $font-weight;\n  font-style: $font-style;\n}\n\n@mixin antialiased {\n  font-smoothing: antialiased;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n@mixin font-face(\n  $font-family,\n  $file-path,\n  $font-weight: normal,\n  $font-style: normal,\n  $fallback: sans-serif\n) {\n  $file-path: '@assets/fonts/#{$file-path}';\n\n  @font-face {\n    font-family: '#{$font-family}';\n\n    src: url($file-path + '.eot');\n    src: local($file-path),\n      url($file-path + '.eot?#iefix') format('embedded-opentype'),\n      url($file-path + '.woff2') format('woff2'),\n      url($file-path + '.woff') format('woff');\n\n    font-weight: $font-weight;\n    font-style: $font-style;\n    font-display: swap;\n  }\n\n}\n\n@mixin fadeKeyframes($type: \"fadeIn\", $name: \"fade\") {\n  @-webkit-keyframes #{$name} {\n    @content;\n  }\n  @-moz-keyframes #{$name} {\n    @content;\n  }\n  @-ms-keyframes #{$name} {\n    @content;\n  }\n  @keyframes #{$name} {\n    @content;\n  }\n}","$pure-white-color: #FFFFFF !important;\n$pure-black-color: #000000 !important;\n$lighter-black-color: #1D1F23 !important;\n$white-color: #FAFAFA !important;\n$transparent-color: transparent !important;\n\n$accented-gray-color: #F4F4F4 !important;\n$accented-grayish-gray-color: #FCFCFC !important;\n$accented-low-opasity-grayish-gray-color: #fcfcfcae !important;\n$text-light-gray-color: #D7D7D7 !important;\n$lighter-gray-color: #D3CDCA !important;\n$milky-white-color: #DDDDDD !important;\n$darker-gray-color: #5D5D5D !important;\n$dark-gray-color: #4D4D4D !important;\n$darkish-gray-color: #A3A3A3 !important;\n$transparent-redish-gray-color: #db34242e !important;\n$checkbox-light-grey: #6d6d6d !important;\n\n$normal-gray-color: #7F7F7F !important;\n$normal-darker-gray-color: #7E7E7E !important;\n\n$dark-red-color: #891F16 !important;\n$accented-red-color: #E04031 !important;\n$text-red-color: #A4271B !important;\n$ant-menu-color: #E13F30 !important;\n\n$slider-base-dot-color: #D9D9D9 !important;\n$timeline-reel-color: #D9D9D9 !important;\n$copyright-section-color: #C4C4C4 !important;\n\n$selected-arrow-color: #C4C4C4 !important;\n$base-arrow-color: #F2F2F2 !important;\n\n$base-arrow-filter-color: invert(97%) sepia(66%) saturate(423%)\n  hue-rotate(212deg) brightness(113%) contrast(90%) !important;\n$selected-arrow-filter-color: invert(87%) sepia(0%) saturate(479%)\n  hue-rotate(143deg) brightness(95%) contrast(81%) !important;\n$hovered-btn-filter-color: drop-shadow(0 2px 15px rgba(61, 0, 0, .25)) !important;\n\n$card-scrollbar-track-color: #DEDCDC !important;\n$modal-hover-color: #ECE9E9 !important;\n$burger-menu-color: #7F7F7F !important;\n$input-outline-color: #E6E4E4 !important;\n$light-border-color: #DCDCDC !important;\n$partners-mobile-background-color: #FCFCFC !important;\n$streetcode-background-color:#FCFCFC !important;\n$catalog-background-color: #F5F5F5 !important;\n","$code-tag-font: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;\n$body-tag-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\nsans-serif;\n\n$roboto-font: 'Roboto', 'Helvetica Neue', sans-serif !important;\n$roboto-light: 'Roboto-Light', 'Helvetica Neue', sans-serif;\n$roboto-font-style: normal;\n$closer-text-font: 'Closer Text', $roboto-font;\n"],"sourceRoot":""}]);
