ion: column;\n  justify-content: center;\n  align-items: center;\n\n  > h1 {\n    color: c.$accented-red-color;\n    @include mut.with-font(ft.$closer-text-font, $font-size: 40px, $font-weight: 500);\n  }\n\n  > h3 {\n    @include mut.with-font($font-weight: 300);\n  }\n\n  .enterSum{\n    @include mut.rem-margined($top: 32px);\n    @include mut.with-font(ft.$closer-text-font, $font-size: 15px, $font-weight: 500);\n    line-height: f.pxToRem(18px);\n    color: c.$darkish-gray-color;\n  }\n\n  > .donateInputContainerWrapper{\n    position: relative;\n    display: flex;\n    justify-content: center;\n    &::before {\n      content: '';\n      position: absolute;\n      bottom: 0;\n      width: 100%;\n      border-bottom: 2px solid c.$darkish-gray-color;\n      transform: translateY(-52px);\n    }\n    &.active::before {\n      content: none;\n    }\n  }\n\n  .amountInput {\n    @include mut.rem-margined($bottom: 35px);\n    @include mut.with-font(ft.$closer-text-font, $font-size: 64px, $font-weight: 500);\n    text-align: right;\n    --input-width: 42px;\n    color: c.$darkish-gray-color;\n    border: none;\n    padding: 0;\n\n    &.active {\n      color: c.$pure-black-color;\n      text-shadow: 1px 0 1px c.$pure-black-color;\n    }\n\n    &:focus {\n      outline: none;\n    }\n  }\n\n  .GryvnaSymbol{\n    width: fit-content !important;\n  }\n\n  .donatesBtnContainer {\n    @include mut.sized(686px, 64px);\n    @include mut.flexed($direction: row-reverse, $justify-content: space-between, $gap: 25px);\n\n    > * {\n      @include mut.sized(212px, inherit);\n      @include mut.full-rounded(15px);\n      @include mut.flex-centered();\n\n      border-radius: 15px !important;\n      border: 3px solid c.$dark-red-color;\n      @include vnd.vendored(transition, 'background-color .75s ease');\n      filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.19)); \n\n      > span {\n        @include vnd.vendored(transition, 'color .75s ease');\n        @include mut.with-font(ft.$closer-text-font, $font-size: 36px, $font-weight: 350);\n        color: c.$pure-black-color;\n      }\n\n      &:where(:hover, &.active) {\n        background-color: c.$accented-red-color;\n        border: 3px solid c.$accented-red-color;\n\n        > span {\n          color: c.$pure-white-color;\n          font-weight: 500;\n        }\n      }\n      &:focus{\n          outline: none;\n        }\n    }\n  }\n\n  .donatesInputContainer {\n    @include mut.sized(686px, 64px);\n    @include mut.flexed($justify-content: space-between, $gap: 25px);\n    margin: f.pxToRem(12px) 0;\n\n    >.checkbox-borderline {\n      border: none;\n      display: flex;\n      align-items: center;\n      \n      \n      .ant-checkbox-input {\n        @include mut.sized(24px, 24px);\n      }\n      \n      span{\n        display: inline-block;\n        vertical-align:baseline;\n                   \n        .ant-checkbox-inner {\n          @include mut.sizedImportant(24px, 24px);\n        }\n  \n        &:last-child {\n          @include mut.with-font(ft.$roboto-font, $font-size: 15px, $font-weight: 300);\n\n          font-style: normal;\n          line-height: f.pxToRem(18px);\n          padding-inline-start: f.pxToRem(16px);\n          padding-top: f.pxToRem(2px);\n          height: f.pxToRem(15px);\n          text-align: left;\n        }\n      }\n    }\n\n    > * {\n      @include mut.full-rounded(11px);\n      @include mut.with-font($font-weight: 300);\n    }\n  }\n\n  .donatesDonateBtn {\n    @include mut.sized(212px, 73px);\n    @include mut.full-rounded(15px);\n    background-color: c.$accented-red-color;\n    box-shadow: 0 4px 4px rgba(0, 0, 0, .25);\n    border-radius: f.pxToRem(15px) !important;\n    border: none;\n    color: c.$pure-white-color;\n    @include mut.with-font(ft.$roboto-font, $font-size: 20px, $font-weight: 500);\n\n    &:disabled{\n      background-color: c.$lighter-gray-color;\n      color: c.$pure-white-color;\n      cursor: pointer;\n\n      &:hover {\n        cursor: default;\n        background-color: c.$lighter-gray-color;\n        border: none;\n      }\n    }\n    > span {\n      @include mut.with-font($font-size: 20px, $font-weight: 500);\n      color: c.$pure-white-color;\n    }\n\n    &:hover {\n      background-color: c.$dark-red-color;\n      transition: 0.5s;\n      border: none;\n    }\n\n    &:focus{\n      outline: none;\n      border: none;\n    }\n  }\n}\n\n@media screen and (max-width: 1024px) {\n  .donatesModal {\n    @include mut.sizedImportant(340px, 316px);\n    display: flex;\n    justify-content: center;\n\n    &.ant-modal {\n      .ant-modal-content {\n        @include mut.full-rounded(16px, $overflow: visible);\n\n        .ant-modal-body{\n          width: 100%;\n          padding: f.pxToRem(16px);\n        }\n      }\n\n      .ant-modal-close{\n        display: none;\n      }\n    }\n\n    .donatesModalContent {\n      > h1{\n        @include mut.with-font(ft.$closer-text-font, $font-size: 20px, $font-weight: 500);\n        line-height: 26px;\n        text-align: center;\n        letter-spacing: 0.005em;\n      }\n\n      > h3{\n        @include mut.with-font($font-size: 14px, $font-weight: 300);\n        @include mut.rem-margined($top: 10px, $bottom: 10px);\n      }\n\n      .enterSum{\n        @include mut.rem-margined($top: 0);\n        @include mut.with-font(ft.$closer-text-font, $font-size: 10px, $font-weight: 500);\n        line-height: 15px;\n      }\n\n      .donateInputContainerWrapper{\n        &::before {\n          transform: translateY(-20px);\n        }\n        &:not(.active) {\n\n        }\n      }\n\n      .amountInput{\n        @include mut.with-font(ft.$closer-text-font, $font-size: 32px, $font-weight: 500);\n        @include mut.rem-margined($top: 0px, $bottom: 16px);\n\n        display: flex;\n        align-items: center;\n        height: 42px;\n        max-width: 100%;\n\n      }\n      .GryvnaSymbol{\n        padding-top: 1px !important;\n      }\n      .donatesBtnContainer{\n        @include mut.sized(308px, 100%);\n        \n        gap: 0;\n        > * {\n          width: f.pxToRem(100px) !important;\n          height: f.pxToRem(37px) !important;\n          border-radius: 11px !important;\n          border: 2px solid c.$dark-red-color;\n\n          > span{\n            @include mut.with-font(ft.$closer-text-font, $font-size: 16px, $font-weight: 350);\n          }\n        }\n      }\n\n      .donatesDonateBtn {\n        @include mut.sized(100%, 46px);\n        @include mut.full-rounded(10px);\n\n        > span{\n          @include mut.with-font(ft.$roboto-font, $font-size: 14px, $font-weight: 500);\n          line-height: 16px;\n          color: #FFFFFF;\n          text-transform: none;\n          border-radius: 10px !important;\n        }\n      }\n\n      .donatesInputContainer{\n        @include mut.sized(100%, 28px);\n        margin: 16px;\n        \n        >.checkbox-borderline {\n          display: flex;\n          overflow: visible !important;\n\n          > span{\n            display: inline-block;\n            vertical-align: middle;\n      \n            &:last-child {\n              @include mut.with-font(ft.$roboto-font, $font-size: 12px, $font-weight: 500);\n            \n              height: f.pxToRem(28px);\n              padding-top: f.pxToRem(2px);\n              font-style: normal;\n              line-height: f.pxToRem(14px);\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n@media screen and (max-width: 480px) {\n  .donatesModal .donatesModalContent .donatesBtnContainer {\n    @include mut.sized(308px, 100%);\n    @include mut.flexed($direction: row-reverse, $justify-content: space-between, $gap: 4px);\n\n    > * {\n      width: f.pxToRem(100px) !important;\n    }\n  }\n}","@use '../variables/variables.fonts' as ft;\n@use '../variables/variables.colors' as c;\n@use '../mixins/vendor.mixins' as vnd;\n@use '../utils.functions' as f;\n\n// --------------------------Positioning--------------------------\n@mixin flexed(\n  $direction: row,\n  $align-items: stretch,\n  $justify-content: stretch,\n  $wrap: nowrap,\n  $gap: 0\n) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify-content;\n  align-items: $align-items;\n  flex-wrap: $wrap;\n  gap: f.pxToRem($gap);\n}\n\n\n@mixin set-positioning($top, $right, $bottom, $left) {\n  @if ($top == 'auto' or unit($top) == '%') {\n    top: $top;\n  } @else {\n    top: f.pxToRem($top);\n  }\n  @if ($right == 'auto' or unit($right) == '%') {\n    right: $right;\n  } @else {\n    right: f.pxToRem($right);\n  }\n  @if ($bottom == 'auto' or unit($bottom) == '%') {\n    bottom: $bottom;\n  } @else {\n    bottom: f.pxToRem($bottom);\n  }\n  @if ($left == 'auto' or unit($left) == '%') {\n    left: $left;\n  } @else {\n    left: f.pxToRem($left);\n  }\n}\n\n@mixin positioned-as($position: relative, $top: auto, $right: auto, $bottom: auto, $left: auto) {\n  position: $position;\n  @include set-positioning($top, $right, $bottom, $left);\n}\n\n@mixin sized($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width ;\n  } @else {\n    width: f.pxToRem($width) ;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height ;\n  } @else {\n    height: f.pxToRem($height);\n  }\n}\n@mixin sizedImportant($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width !important ;\n  } @else {\n    width: f.pxToRem($width) !important ;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height !important ;\n  } @else {\n    height: f.pxToRem($height) !important;\n  }\n}\n\n@mixin parent-sized {\n  @include sized(100%, 100%);\n}\n\n// --------------------------Centering----------------------------\n@mixin flex-centered($direction: row, $gap: 0) {\n  @include flexed($direction, center, center, nowrap, $gap);\n}\n\n@mixin absolute-centered($axes: 'both') {\n  position: absolute;\n\n  @if $axes == 'vertical' or $axes == 'v' {\n    top: 50%;\n    @include vnd.vendored(transform, translateY(-50%));\n  }\n  @else if $axes == 'horizontal' or $axes == 'h' {\n    left: 50%;\n    @include vnd.vendored(transform, translateX(-50%));\n  }\n  @else if $axes == 'both' or $axes == 'b' {\n    top: 50%;\n    left: 50%;\n    @include vnd.vendored(transform, translate(-50%, -50%));\n  }\n}\n\n// --------------------------Bordering----------------------------\n@mixin rounded(\n  $top-left: 0,\n  $top-right: 0,\n  $bottom-right: 0,\n  $bottom-left: 0,\n  $overflow: hidden\n) {\n  $rad: $top-left $top-right $bottom-right $bottom-left;\n\n  border-radius: $rad !important;\n  -webkit-border-radius: $rad !important;\n\n  overflow: $overflow !important;\n  -webkit-background-clip: padding-box;\n}\n@mixin border($color:\"#000\", $border-width:1px, $type:solid ){\n  border:  f.pxToRem($border-width) $type $color;\n}\n\n@mixin full-rounded($rad, $overflow: hidden) {\n  @include rounded($rad, $rad, $rad, $rad, $overflow);\n}\n\n@mixin circular($radius, $color: c.$pure-white-color) {\n  @include sized($radius, $radius);\n  @include full-rounded(50%);\n  background: $color;\n}\n\n// --------------------------Margins-----------------------------\n@mixin rem-margined($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  margin: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n\n@mixin rem-padded($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  padding: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n// --------------------------Appearance----------------------------\n@mixin backdrop-filtering-blur($f-size){\n  -webkit-backdrop-filter: blur($f-size);\n  -o-backdrop-filter: blur($f-size);\n  -moz-backdrop-filter: blur($f-size);\n\n  backdrop-filter: blur($f-size);\n}\n\n@mixin outlined($b-size: 2px, $color: red) {\n  outline: