;\n  }\n  @keyframes #{$name} {\n    @content;\n  }\n}","$code-tag-font: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;\n$body-tag-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\nsans-serif;\n\n$roboto-font: 'Roboto', 'Helvetica Neue', sans-serif !important;\n$roboto-light: 'Roboto-Light', 'Helvetica Neue', sans-serif;\n$roboto-font-style: normal;\n$closer-text-font: 'Closer Text', $roboto-font;\n"],"sourceRoot":""}]);
