ize: 20px);\n                    }\n                }\n            }              \n\n            .purposeBottomText {\n                @include mut.with-font($font-family: ft.$roboto-font, $font-weight: 300, $font-size: 20px);\n                line-height: 27px;\n\n                p {\n                    @include mut.rem-margined($top: 16px);\n                }\n            }\n        }\n\n        .donateWindow {\n            @include mut.sized($height: 525px, $width: 590px);\n            vertical-align: top;\n            background: c.$pure-white-color;\n            \n            box-shadow: 0px 7px 17px 2px rgba(91, 91, 91, 0.25);\n            @include mut.full-rounded(32px);\n        }\n    }\n    \n    .donateSubBlocks {\n        @include mut.sized($height: 351px, $width: 1200px);\n        @include mut.flexed($justify-content: space-around, $align-items: center, $gap: 100px);\n\n        margin: 80px calc((75vw - 1200px) / 2) 0;\n\n        .block {\n            .heading {\n                @include mut.with-font($font-family: ft.$closer-text-font, $font-weight: 500, $font-size: 36px);\n                line-height: 48px;\n                text-align: center;\n            }\n            .content {\n                @include mut.sized($height: 230px);\n                @include mut.flexed($direction: column, $justify-content: center);\n                @include mut.with-font($font-family: ft.$roboto-font, $font-weight: 300, $font-size: 20px);\n                line-height: pxToRem(26.84px);\n\n                svg {\n                    margin: 0 18.5px;\n                }\n\n                .accountInfo {\n                    @include mut.rem-margined($top: 16px);\n                    line-height: pxToRem(23.44px);\n\n                    .thickerText {\n                        @include mut.rem-margined($top: 8px);\n                        @include mut.with-font($font-family: ft.$closer-text-font, $font-weight: 500, $font-size: 20px);\n                        line-height: pxToRem(26.84px);\n                    }\n                }\n            }\n\n            button {\n                width: 100%;\n\n                &:hover {\n                    background-color: c.$dark-red-color;\n                }\n            }\n\n            &.forCompanies {\n                @include mut.sized($width: 340px);\n            }\n\n            &.bankAccount {\n                @include mut.sized($width: 442px);\n            }\n        }\n    }\n\n    .bottomText {\n        @include mut.rem-padded($top: 80px, $bottom: 100px);\n        text-align: center;\n        @include mut.with-font($font-family: ft.$closer-text-font, $font-weight: 500, $font-size: 32px);\n        line-height: 42px;\n        color: c.$text-red-color;\n    }\n\n    .supportButton {\n        height: 73px;\n\n        background: c.$accented-red-color;\n        box-shadow: 0px 4px 4px c.$transparent-redish-gray-color;\n        @include mut.full-rounded(14px);\n        border: none;\n\n        @include mut.with-font($font-family: ft.$roboto-font, $font-weight: 500, $font-size: 20px);\n        line-height: 23px;\n        color: c.$pure-white-color;\n        text-align: center;\n\n        &.withSvg {\n            @include mut.flexed($align-items: center, $justify-content: center, $gap: 18px);\n        }\n\n        &:hover {\n            background-color: c.$dark-red-color;\n        }\n    }\n\n    .titleSmall {\n        @include mut.with-font($font-family: ft.$closer-text-font, $font-weight: 500, $font-size: 32px);\n        line-height: 42px;\n    }\n}\n\n@media screen and (max-width: 1400px) {\n    .supportUsPageWrapper {\n        padding: 110px 10% 0;\n\n        .mainBlock {\n            .donatesPurposeBlock {\n                width: calc(80vw - 348px);\n\n                .heading {\n                    @include mut.with-font($font-family: ft.$closer-text-font, $font-weight: 500, $font-size: 28px);\n                    line-height: 32px;\n                }\n\n                ul.purpo