n.heightContainer {\n    padding-top: pxToRem(23px);\n}\n\n.slick-center .interestingFactSlide {\n    transform: scale(1.52);\n}\n\n.singleSlideContainer{\n    margin-bottom: pxToRem(90px);\n    @include mut.flex-centered();\n    @include vnd.vendored(transform, scale(1.32));\n    padding-top: pxToRem(88px);\n    \n    .interestingFactSlide{\n        @include  mut.sized(650px, 600px);\n\n        .slideImage {\n            @include mut.sized($height: 60%);\n\n            img {\n                @include mut.sized($width: 100%);\n                max-height: 100%;\n            }\n        }\n\n        .slideText{\n            @include mut.sized($height: 40%);\n            \n            .heading{\n                font-size:26px;\n            }\n                      \n            .mainText{\n                @include mut.sized($height: 75%);\n                font-size:15px;\n                line-height: pxToRem(20px);\n                @include mut.rem-margined($right: 5px );\n\n                \n                &::-webkit-scrollbar-thumb,\n                &::-webkit-scrollbar-track {\n                  @include mut.full-rounded(15px);\n                  @include mut.sized($height: 20px);\n\n                }\n              \n                &::-webkit-scrollbar-thumb {\n                  background: c.$card-scrollbar-track-color;\n                }\n              \n                &::-webkit-scrollbar-track {\n                  background: c.$accented-gray-color;\n                  @include mut.rem-margined( $bottom: 10px );              \n                }\n            }\n        }\n    }\n}\n\n@media screen and (max-width: 1450px){\n    .interestingFactsSliderContainer {\n        @include mut.sized(930px);\n    }\n\n    .interestingFactsSliderContainer {\n        .slick-dots{\n            bottom: auto;\n        }\n    }\n}\n\n@media screen and (max-width: 1200px){\n    .interestingFactsSliderContainer {\n        @include mut.sized(830px);\n    }\n}\n\n@media screen and (max-width: 1024px){\n    .interestingFactsSliderContainer .slick-list{\n        padding: 4.125rem 0 !important;\n    }\n\n    .interestingFactsWrapper {\n        .slick-dots{         \n            @include mut.rem-margined(0);\n            bottom: 15%;\n        }\n    }\n\n    .interestingFactsContainer {\n        @include mut.sized(92%, 100%); \n        @include mut.flex-centered($direction: column);\n    \n        .blockHeadingContainer {\n            margin-left: pxToRem(10px);\n        }\n    }\n    .interestingFactsSliderContainer {\n        @include mut.sized(85%, 100%);\n    }\n\n    .singleSlideContainer{\n        .interestingFactSlide{\n            @include mut.sized(445px); \n            @include mut.full-rounded(20px);\n    \n            .slideImage {\n                @include mut.sized($height: 65%);\n    \n                img {\n                    @include mut.sized($width: 100%, $height: 100%);\n                    max-height: 300px;\n                }\n            }\n            .slideText{\n                @include mut.sized($height: 40%);\n                .heading{\n                    font-size:20px;\n                    @include mut.rem-padded(7px, 0px, 8px, 15px);\n                }\n    \n                .mainText{\n                    overflow: auto;\n                    height: 150px;\n                    font-size: 15px;\n                    line-height: pxToRem(21px);\n                    margin-bottom: pxToEm(10px);\n    \n                    &:after {\n                        content: \"\";\n                        position: absolute;\n                        z-index: 1;\n                        bottom: 0;\n                        left: 0;\n                        pointer-events: none;\n                        background-image: linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255, 1) 90%);\n                        width: 100%;\n                        height: 1.7em;\n                      }\n                }\n            }\n        }\n    }\n}\n\n@media screen\nand (min-width: 768px) and (max-width: 1024px){\n\n.interestingFactsWrapper {\n    @include wr.main-page-block-wrapper();\n    .slick-dots{\n        @include mut.rem-margined(0);\n        bottom: 15%;\n    }\n  \n}\n    .interestingFactsContainer {\n        @include mut.sized(765px, 100%); \n        @include mut.flex-centered($direction: column);\n        margin: 0 auto;\n    }\n    .interestingFactsSliderContainer {\n        @include mut.sized(100%, 100%); \n    }\n\n    .singleSlideContainer{\n \n        .interestingFactSlide{\n            @include mut.sized(445px);\n    \n            @include mut.full-rounded(20px);\n    \n            .slideImage {\n                @include mut.sized($height: 65%);\n    \n                img {\n                    @include mut.sized($width: 100%, $height: 100%);\n                    max-height: 210px;\n                }\n            }\n            .slideText{\n                @include mut.sized($height: 40%);\n                .heading{\n                    font-size:20px;\n                    @include mut.rem-padded(7px, 0px, 8px, 15px);\n                }\n    \n                .mainText{\n                    overflow: auto;\n                    height: 150px;\n                    font-size: 15px;\n                    line-height: pxToRem(21px);\n                    margin-bottom: pxToEm(10px);\n    \n                    &:after {\n                        content: \"\";\n                        position: absolute;\n                        z-index: 1;\n                        bottom: 0;\n                        left: 0;\n                        pointer-events: none;\n                        background-image: linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255, 1) 90%);\n                        width: 100%;\n                        height: 1.7em;\n                      }\n    \n                }\n            }\n        }\n    }\n}\n\n@media screen and (max-width: 768px){\n    .interestingFactsWrapper {\n        @include wr.main-page-block-wrapper();\n        .slick-dots{\n            @include mut.rem-margined(0);\n            bottom: 15%;\n        }\n    }\n\n    .interestingFactsContainer {\n        @include mut.sized(765px, 100%); \n        @include mut.flex-centered($direction: column);\n    \n        .blockHeadingContainer {\n            margin-left: pxToRem(10px);\n        }\n    }\n    .interestingFactsSliderContainer {\n        @include mut.sized(100%, 100%); \n    }\n\n\n.singleSlideContainer{\n    margin-bottom: pxToRem(65px);\n    padding-top: pxToRem(50px);\n\n    .interestingFactSlide{\n        @include mut.sized(345px);\n        @include mut.full-rounded(20px);\n\n        .slideImage {\n            @include mut.sized($height: 65%);\n\n            img {\n                @include mut.sized($width: 100%, $height: 100%);\n                max-height: 210px;\n            }\n        }\n        .slideText{\n            @include mut.sized($height: 40%);\n            .heading{\n                font-size:14px;\n                @include mut.rem-padded(7px, 0px, 8px, 15px);\n            }\n\n            .mainText{\n                overflow: auto;\n                font-size: 12px;\n                line-height: pxToRem(21px);\n                margin-bottom: pxToEm(10px);\n\n                &:after {\n                    content: \"\";\n                    position: absolute;\n                    z-index: 1;\n                    bottom: 0;\n                    left: 0;\n                    pointer-events: none;\n                    background-image: linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255, 1) 90%);\n                    width: 100%;\n                    height: 1.7em;\n                  }\n\n            }\n        }\n    }\n}\n}\n\n@media screen and (max-width: 480px){\n    .singleSlideContainer{\n        margin-bottom: 3.0625rem;\n        padding-top: 2.125rem;\n    }\n\n    .interestingFactsContainer {\n        @include mut.sized(100%, 100%);\n\n        .single{\n            margin-bottom: pxToRem(100px) ;\n        }\n    }\n\n    .interestingFactsWrapper {\n        @include wr.main-page-block-wrapper();\n        .slick-dots{\n            bottom: 15%;\n        }\n    }\n\n    .interestingFactsSliderContainer {\n        @include mut.sized(770px, 100%); \n    }\n\n    .interestingFactSlide{\n        .slideImage {\n            @include mut.sized($height: 45%);\n            img {\n                @include mut.sized($width: 100%);\n                max-height: 210px;\n            }\n        }\n    }\n\n    .singleSlideContainer{\n        .interestingFactSlide{\n            @include mut.sized(32%);\n            @include mut.full-rounded(20px);\n    \n            .slideImage {\n                @include mut.sized($height: 55%);\n    \n                img {\n                    @include mut.sized($width: 100%, $height: 100%);\n                }\n            }\n            .slideText{\n                @include mut.sized($height: 110px);\n                @include mut.full-rounded(0px);\n            }\n        }\n    }\n}\n\n\n","@use '../variables/variables.fonts' as ft;\n@use '../variables/variables.colors' as c;\n@use '../mixins/vendor.mixins' as vnd;\n@use '../utils.functions' as f;\n\n// --------------------------Positioning--------------------------\n@mixin flexed(\n  $direction: row,\n  $align-items: stretch,\n  $justify-content: stretch,\n  $wrap: nowrap,\n  $gap: 0\n) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify-content;\n  align-items: $align-items;\n  flex-wrap: $wrap;\n  gap: f.pxToRem($gap);\n}\n\n\n@mixin set-positioning($top, $right, $bottom, $left) {\n  @if ($top == 'auto' or unit($top) == '%') {\n    top: $top;\n  } @else {\n    top: f.pxToRem($top);\n  }\n  @if ($right == 'auto' or unit($right) == '%') {\n    right: $right;\n  } @else {\n    right: f.pxToRem($right);\n  }\n  @if ($bottom == 'auto' or unit($bottom) == '%') {\n    bottom: $bottom;\n  } @else {\n    bottom: f.pxToRem($bottom);\n  }\n  @if ($left == 'auto' or unit($left) == '%') {\n    left: $left;\n  } @else {\n    left: f.pxToRem($left);\n  }\n}\n\n@mixin positioned-as($position: relative, $top: auto, $right: auto, $bottom: auto, $left: auto) {\n  position: $position;\n  @include set-positioning($top, $right, $bottom, $left);\n}\n\n@mixin sized($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width ;\n  } @else {\n    width: f.pxToRem($width) ;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height ;\n  } @else {\n    height: f.pxToRem($height);\n  }\n}\n@mixin sizedImportant($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width !important ;\n  } @else {\n    width: f.pxToRem($width) !important ;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height !important ;\n  } @else {\n    height: f.pxToRem($height) !important;\n  }\n}\n\n@mixin parent-sized {\n  @include sized(100%, 100%);\n}\n\n// --------------------------Centering----------------------------\n@mixin flex-centered($direction: row, $gap: 0) {\n  @include flexed($direction, center, center, nowrap, $gap);\n}\n\n@mixin absolute-centered($axes: 'both') {\n  position: absolute;\n\n  @if $axes == 'vertical' or $axes == 'v' {\n    top: 50%;\n    @include vnd.vendored(transform, translateY(-50%));\n  }\n  @else if $axes == 'horizontal' or $axes == 'h' {\n    left: 50%;\n    @include vnd.vendored(transform, translateX(-50%));\n  }\n  @else if $axes == 'both' or $axes == 'b' {\n    top: 50%;\n    left: 50%;\n    @include vnd.vendored(transform, translate(-50%, -50%));\n  }\n}\n\n// --------------------------Bordering----------------------------\n@mixin rounded(\n  $top-left: 0,\n  $top-right: 0,\n  $bottom-right: 0,\n  $bottom-left: 0,\n  $overflow: hidden\