___CSS_LOADER_EXPORT___.push([module.id, ".privatePolicyContainer .titleContainer {\n  margin-bottom: 3.75rem;\n}\n.privatePolicyContainer .titleContainer .titleSmall {\n  font-family: \"Roboto\", \"Helvetica Neue\", sans-serif !important;\n  font-size: 0.75rem;\n  font-weight: 300;\n  font-style: normal;\n  line-height: 14px;\n  color: #891F16;\n  text-align: right;\n  text-transform: uppercase;\n}\n.privatePolicyContainer .titleContainer .titleBig {\n  font-family: \"Closer Text\", \"Roboto\", \"Helvetica Neue\", sans-serif !important;\n  font-size: 2.5rem;\n  font-weight: 500;\n  font-style: normal;\n  line-height: 53px;\n  color: #1D1F23;\n  margin-top: 1.5rem;\n}\n.privatePolicyContainer .titleContainer .subTitle {\n  font-family: \"Closer Text\", \"Roboto\", \"Helvetica Neue\", sans-serif !important;\n  font-size: 1.25rem;\n  font-weight: 500;\n  font-style: normal;\n  line-height: 26px;\n  color: #891F16;\n  margin-top: 1rem;\n}\n\n@media screen and (max-width: 1024px) {\n  .privatePolicyContainer .titleContainer .titleBig {\n    font-family: \"Closer Text\", \"Roboto\", \"Helvetica Neue\", sans-serif !important;\n    font-size: 2rem;\n    font-weight: 500;\n    font-style: normal;\n    line-height: 42px;\n  }\n  .privatePolicyContainer .titleContainer .subTitle {\n    font-family: \"Roboto\", \"Helvetica Neue\", sans-serif !important;\n    font-size: 0.875rem;\n    font-weight: 500;\n    font-style: normal;\n    line-height: 16px;\n  }\n}", "",{"version":3,"sources":["webpack://./src/features/AdditionalPages/PrivatePolicyPage/Title/Title.styles.scss","webpack://./src/assets/sass/mixins/_utils.mixins.scss","webpack://./src/assets/sass/variables/_variables.fonts.scss"],"names":[],"mappings":"AAKI;EACI,sBAAA;AAJR;AAMQ;ECiMN,8DCpMY;EDsMZ,kBAAA;EACA,gBDnM8E;ECoM9E,kBCtMkB;EFGR,iBAAA;EACA,cAAA;EACA,iBAAA;EACA,yBAAA;AADZ;AAIQ;ECyLN,6ECjMiB;EDmMjB,iBAAA;EACA,gBD3LmF;EC4LnF,kBCtMkB;EFWR,iBAAA;EACA,cAAA;EACA,kBAAA;AACZ;AAEQ;ECkLN,6ECjMiB;EDmMjB,kBAAA;EACA,gBDpLmF;ECqL